<template>
  <router-view :windowWidth="windowWidth" :windowHeight="windowHeight"/>
</template>

<script>
import store from "./store";


const request = require('request');

const dev = process.env.NODE_ENV === 'development';
import projects from '../public/project.json';

if(dev){  
  //store.state.projects = projects;
  console.log('loading development env');
  store.commit('initProjectList', projects);
}else{
  console.log('loading production env');
  const url = 'https://eia-moe.github.io/map-dist/project.json';
  const options = { json: true };

  request(url, options, (error, res, body) => {
    if (error) {
        return  console.log(error)
    };
    if (!error && res.statusCode == 200) {
        store.commit('initProjectList', body);
    };
  });
}



export default {
    name: 'App',
    data() {
        
        return {
            windowHeight: window.innerHeight,
            windowWidth: window.innerWidth,
        }
    },

    mounted() {
        this.$nextTick(() => {
            window.addEventListener('resize', this.onResize);
            store.commit('windowSize', window.innerWidth, window.innerHeight);
        })
    },

    beforeDestroy() { 
        window.removeEventListener('resize', this.onResize); 
    },

    methods: {  
        onResize() {
            this.windowHeight = window.innerHeight;
            this.windowWidth = window.innerWidth;
            store.commit('windowSize', this.windowWidth, this.windowHeight);
        }
    }
}
</script>

<style>
@import url('https://fonts.googleapis.com/css2?family=Moul&family=Nokora:wght@100;400&display=swap');
body {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
}

#app {
  width: 100%;
  height: 100%;
}
</style>
